
h1 {color: #42b983;
}
.infoA {background-color: #fff;padding: 32px;
}
.infoA img {width: 215px;/*  height: 285px; */
}
.infoA .main {background-color: #dbe9f9;padding: 35px;font-size: 24px
}
.infoA .main span {display: block;line-height: 30px;color: #5a6067;margin-bottom: 25px;
}
.infoA .main span:last-child {margin-bottom: 0
}
.infoA .main span em {color: #000;border-bottom: #808892 1px solid;padding-bottom: 5px;width: 70%;display: inline-block;text-align: center;
}
.infoA .i2 p {margin-top: 20px;
}
.infoA .i2 p span {width: 48%;display: inline-block;
}
.infoB {background-color: #dbe9f9
}
.infoB .title {background-color: #f6f6f6; border-bottom: rgba(0,0,0,.2) 1px solid; overflow: hidden;padding: 20px 30px;
}
.infoB .title .l {float: left; color: rgba(0,0,0,.56);font-size: 30px;
}
.infoB .title .l i {background-color: #4a90e2; width: 16px; height: 16px; margin-right: 10px
}
.infoB .title .r {float: right; color: #4a90e2;font-size: 24px;
}
.infoB .main {padding: 20px 0 20px 35px;
}
.infoB p {border:1px solid #4a90e2; padding: 10px 20px; margin:15px 35px 20px 0; background-color: #fff;color: #4a90e2;font-size: 22px
}
.infoB p span {display: block; padding: 3px 0
}
.infoB .tit {color: rgba(0,0,0,.56); font-size: 20px
}
.infoB .button {background-color: #f6f6f6; text-align: center; padding: 25px 0;
}
.infoB button {border:2px solid #4285f4;border-radius:6px; padding: 12px 70px;color: #4a90e2;font-size: 20px;background-color: #fff
}
.infoB button:hover {background-color: #4285f4; color: #fff
}
.radios {min-height:300px;width:600px; font-size: 24px;color: rgba(0,0,0,.54)
}
.radios li {
}
.radios label {padding: 30px 0 30px 35%;display: block;
}
.radios li.active {background:#d9e7fd;color:#4787f2
}
.radios input {display: none;
}
.radios i {margin-right: 15px;-webkit-appearance: none;background-color: #fff;border: 1px solid #616161;padding: 9px;border-radius: 50px;display: inline-block;position: relative;
}
.radios li.active i {border: 1px solid #3079f2;
}
.radios li.active i:after {content: ' ';width: 12px;height: 12px;border-radius: 50px;position: absolute;top: 3px;background: #3079f2;box-shadow: inset 0px 0px 10px #3079f2;text-shadow: 0px;left: 3px;font-size: 32px;
}
.buttons{display:flex;justify-content:center;padding: 25px 0; color: #4285f4; font-size: 20px;border-top:1px solid #979797;
}



