
.lssj {width: 100%; text-align: center; border: none;font-size: 18px; border-bottom: rgba(0,0,0,.12) 1px solid;
}
.lssj tr {
}
.lssj tr:nth-child(odd) td {background:#f1f1f1;
}
.lssj th,.lssj td {border-right: rgba(0,0,0,.12) 1px solid;
}
.lssj th:last-child,.lssj td:last-child {border: none;
}
.lssj th {padding: 31px 0 25px 0;
}
.lssj td {padding: 18px 0;
}
.lssj .index {background-color: #4a90e2 !important; opacity: .5
}



